import React, { useEffect, useState } from 'react' // UseState and useEffect new Hooks
import { toast, Zoom } from 'react-toastify' // import somethigns propertys of Toastify
import { db } from '../Firebase' //import Firestore of Firebase
import LinkForm from './LinkForm' //import component
import AcordionComponent from './AcordionComponent'

const Links = () => {

    const [currentId, setCurrentId] = useState('') // Estado de id al onClick de btn Edit
    const [products, setProducts] = useState([]) // Estado Links saved
    const [categories, setCategories] = useState([]);

    const addOrEditProduct = async ({ cantidad, description, name, category }) => {

        if (currentId === '') {
            //Send data to Firestore of Firebase
            const productsRef = db.collection('Products');

            const productsObject = { cantidad, description, name, category };

            productsRef.add(productsObject);
            toast.success('Enlace Agregado!', {
                position: "top-right",
                autoClose: 1200,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                transition: Zoom
            });

        } else {
            await db.collection('Products').doc(currentId).update(products);
            toast.info('Enlace Actualizado!', {
                position: "top-right",
                autoClose: 1200,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                transition: Zoom
            });
            setCurrentId('');
        }
    };

    //Funcion useEffect para renderizar productos dinamicamente
    const getProducts = async () => {
        // Traemos datos de Firebase (onSnapshot => Funcion de Firebase que esta a la escucha de cambios en la base de datos)
        // querySnapshot => modelo o tipo de dato que devuelve Firebase (Study pls !! )
        db.collection('Products').onSnapshot(querySnapshot => {
            setProducts(querySnapshot.docs.map(doc => ({ ...doc.data(), id: doc.id })))
        })
    };


    useEffect(
        () => {
            getProducts();
        }, []
    );

    const onDeleteLink = async (productsId) => {
        if (window.confirm('Estas seguro de quere eliminar este enlace?')) {
            await db.collection('Products').doc(productsId).delete();
            toast.error('Enlace eliminado!', {
                position: "top-right",
                autoClose: 1200,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    };

    return (
        <>
            <div className="row">
                <div className="col-12 col-md-4">
                    <LinkForm {...{ addOrEditProduct, currentId, products, categories, setCategories }} />
                </div>
                <div className="col mt-3">
                    <AcordionComponent {...{ products, onDeleteLink, setCurrentId, categories }} />
                </div>
            </div>
        </>

    )

}
export default Links
