import React, { useEffect, useState } from 'react'
import { BsX } from "react-icons/bs";
import { FiEdit3 } from 'react-icons/fi'
import { db } from '../Firebase' //import Firestore of Firebase

const ShowItems = ({ products, setCurrentId, onDeleteLink, name }) => {

    const [productsByCategory, setProductsByCategory] = useState([])

    const pruebas = async () => {
        const queryRef = db.collection('Products').where('category', '==', name);
        queryRef.onSnapshot(snapshot => {
            setProductsByCategory(snapshot.docs.map(doc => ({ ...doc.data(), id: doc.id }))
            )
        })
    }

    useEffect(() => {
        pruebas();
    })

    return (
        <>
            {
                productsByCategory.map(product => (
                    <div className="col mb-4" key={product.id}>
                        <div className="card border-primary" >
                            <div className="card-body">
                                <div className="d-flex justify-content-between">
                                    <div className="text-wrap">
                                        <h4 className="">{product.name}</h4>
                                    </div>
                                    <div className="text-nowrap ">
                                        <FiEdit3 className="m-2" role="button" style={{ fontSize: "15px" }}
                                            onClick={() =>
                                                setCurrentId(product.id)}>
                                            create
                                        </FiEdit3>
                                        <BsX className="text-danger" role="button" style={{ fontSize: "20px" }}
                                            onClick={() =>
                                                onDeleteLink(product.id)}>
                                            close
                                        </BsX>

                                    </div>
                                </div>
                                <h6 className="card-title">Cantidad: {product.cantidad}</h6>
                                <img className="rounded mx-auto d-block py-2" src="https://via.placeholder.com/150" alt="" />
                                <h6 className="card-subtitle mt-2 text-center">{product.description}</h6>
                            </div>
                        </div>
                    </div>
                ))
            }
        </>
    )
}

export default ShowItems
